        height: 30px;
        padding-top: 1.5vw !important;
    }
    .logo {
        margin-top: 0px;
    }
}

@media (max-width: 848px) {
    .floatingNav {
        height: 35px;
    }
}

@media (max-width: 600px) {
    .floatingNav {
        height: 40px;
    }
}

